
.paginator {
  list-style-type: none;
  padding: 20px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-line-pack: justify;
      align-content: space-between;
}
.paginator ul.pages {
    padding: 0;
    margin: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.paginator ul.pages li {
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      border: 1px solid white;
      color: white;
      background: rgba(255, 255, 255, 0.3);
      min-width: 32px;
      min-height: 32px;
      line-height: 32px;
      text-align: center;
      font-size: 14px;
      vertical-align: middle;
      padding: 0;
      cursor: pointer;
}
.paginator ul.pages li.left, .paginator ul.pages li.right {
        color: #0087ff;
        background: white;
}
.paginator ul.pages li.left.disable, .paginator ul.pages li.right.disable {
          color: rgba(0, 135, 255, 0.4);
          cursor: default;
}
.paginator ul.pages li.left {
        border-top-left-radius: 3px;
        border-bottom-left-radius: 3px;
}
.paginator ul.pages li.right {
        border-top-right-radius: 3px;
        border-bottom-right-radius: 3px;
}
.paginator ul.pages li.active {
        background: transparent;
        cursor: default;
}



