
.accounts-list .table th {
  white-space: nowrap;
}
@media (max-width: 1024px) {
.accounts-list .table td:first-child {
    display: block;
}
.accounts-list .table td:nth-child(2) {
    display: none;
}
.accounts-list .table td:nth-child(3) {
    display: block;
    padding: 3px 0 10px 0;
}
.accounts-list .table td:nth-child(4) {
    font-size: 12px;
}
.accounts-list .table td:nth-child(5) {
    float: right;
}
}



