
@media screen and (max-width: 1024px) {
.btn-group {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
}
.transactions .amount {
  font-size: 18px;
}
.transactions .status i {
  display: inline-block;
  margin-left: 10px;
  width: 22px;
  height: 22px;
  background-size: contain;
  margin-bottom: -4px;
}
.transactions .status.pending i {
  background-image: url(/static/img/pending_transaction.svg);
}
.transactions .custom-date {
  padding: 7px 15px;
}
.transactions .columns .column:last-child {
  text-align: right;
}
@media screen and (max-width: 1024px) {
.transactions {
    padding-top: 15px;
}
.transactions .columns {
    display: block;
}
.transactions .columns .column {
    width: 100%;
}
}



