
.tabs-container .multiselect__tags {
  background: #fff;
  border-color: #fff;
}
.tabs-container ul {
  border-top-right-radius: 6px;
  border-top-left-radius: 6px;
  background: white;
}
.tabs-container ul li.text-body1 {
    padding: 15px 0;
    font-weight: 400;
}
.tabs-container .multiselect {
  display: none;
}
@media (max-width: 1024px) {
.tabs-container .tabs {
    display: none;
}
.tabs-container .multiselect {
    display: block;
    margin-bottom: 22px;
}
}



