
.account-select .multiselect {
  width: 250px;
}
@media screen and (max-width: 1024px) {
.account-select .multiselect {
    width: 100%;
}
}
.account-select .multiselect__option span,
.account-select .multiselect__single span {
  display: block;
}
.account-select .multiselect__option .m-iban,
.account-select .multiselect__option .m-balance,
.account-select .multiselect__single .m-iban,
.account-select .multiselect__single .m-balance {
  font-weight: bold;
}



